import React from 'react'
import { FaBusinessTime } from 'react-icons/fa';
import { ApplicationPeriodGet } from '../../interfaces/application';
import { DATE } from '../../utils/functions';
import Loading from '../Loading/Loading';

interface ApplicationPeriodsProps {
  applicationPeriods: ApplicationPeriodGet[] | null;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  showPeriodFilter: (status: boolean) => void;
}

const ApplicationPeriods = (props: ApplicationPeriodsProps) => {
  return (
    <div className="-mt-4">
      <input type="text" className="input-white border -mt-2 mb-3" placeholder="Search by year, name" />
      {props.applicationPeriods === null ? <Loading /> : props.applicationPeriods.map((item, i) => (
        <div onClick={() => {props.setSelectedPeriod(item); props.showPeriodFilter(false)}} key={i + 1} 
          className={`flex flex-col md:flex-row items-start md:items-center md:justify-between rounded border ${item.is_active === true ? "bg-blue-50 border-blue-100" : "bg-gray-50 border-gray-100 "} hover:bg-blue-100 hover:border-blue-500 px-2 py-2 cursor-pointer mb-2`}>
          <div className="flex flex-row items-center gap-3">
            <FaBusinessTime className={`${item.is_active === true ? "text-blue-600" : "text-gray-500"} text-4xl`} />
            <div className="flex flex-col">
              <div className={`${item.is_active === true ? "text-blue-600" : "text-gray-600"} font-bold`}>{item.application_session_name}</div>
              <div className="text-sm text-gray-500"><span>Status: </span><span className={`${item.is_active === true ? "text-blue-600" : "text-gray-600"}`}>{item.is_active === true ? "Active" : "Expired"}</span></div>
            </div>
          </div>
          <div className="flex flex-col items-start md:items-end md:justify-end">
            <div className="text-xs md:text-sm text-right text-gray-500"><span>Start: </span><span className={`${item.is_active === true ? "text-blue-600" : "text-gray-600"} text-right font-bold`}>{DATE(item.application_start_date, "DD/MM/YYYY")}</span></div>
            <div className="text-xs md:text-sm text-right text-gray-500"><span>End: </span><span className={`${item.is_active === true ? "text-blue-600" : "text-gray-600"} text-right font-bold`}>{DATE(item.application_end_date, "DD/MM/YYYY")}</span></div>
          </div>
        </div>
      ))}
    </div>
  )
}

export default ApplicationPeriods
