import React, { useEffect, useState } from 'react'
import Loading from '../../components/Loading/Loading'
import { connect } from "react-redux";
import {
  Auth, 
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  setSelectedCourse,
  loadQuestionTypes,
} from "../../actions";
import { StoreState } from "../../reducers";
import { course } from '../../interfaces/course';
import { QuestionInterface, uploadQuestionsResponse } from '../../interfaces/question';
import QuestionItem from './QuestionItem';

interface UploadedQuestionsProps {
  questionBank: QuestionBankStoreInterface;
  setUploadedQuestions: (questions: uploadQuestionsResponse | null) => void;
  selectedCourse: course;
  course: courseStoreInterface;
  callBack: (question: QuestionInterface) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  questionsList: QuestionInterface[];
  questionPaper: QuestionInterface[];
}

const _Questions = (props: UploadedQuestionsProps) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [filterQuestionType, setFilterQuestionType] = useState<string>("");
  const [filterQuestionChapter, setFilterQuestionChapter] = useState<string>("");

  const filteredQuestions = (questionType: string, QuestionChapter: string, questions: QuestionInterface[]) => {
    let res: QuestionInterface[] = questions;
    if (questionType !== "" && QuestionChapter !== "") {
      let temp: QuestionInterface[] = questions.filter(itm => itm.type === questionType);
      if (temp.length > 0) {
        res = temp.filter(item => item.chapters.find(x => x === QuestionChapter));
      }
    } else if (questionType !== "") {
      res = questions.filter(itm => itm.type === questionType);
    } else if (QuestionChapter !== "") {
      res = questions.filter(item => item.chapters.find(x => x === QuestionChapter));
    }
    return res
  }

  useEffect(() => {
    if (props.questionTypes.questionTypes === null || props.questionTypes.questionTypes.length === 0) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props]);
  return (
    <div className="bg-white animate__animated animate__fadeInLeft animate__fast px-2 py-2 gap-3 mb-2 rounded">
      <div className="flex flex-row items-center mb-4 gap-2 bg-blue-100 px-2 py-2 -mx-2">
        <div className="flex flex-col w-full">
          <span className="text-gray-700 text-sm font-bold">Question Type</span>
          {props.questionTypes.questionTypes === null ? <Loading /> :
          props.questionTypes.questionTypes.length === 0 ? <div>No Question types found!</div> :
          <select 
          onChange={(e) => {
            setFilterQuestionType(e.target.value);
          }}
          className="input-white border border-gray-400">
            <option>Choose Question type</option>
            {props.questionTypes.questionTypes.map((item, i) => (
              <option key={i + 1} value={item.id}>{item.title}</option>
            ))}
          </select>}
        </div>
        <div className="flex flex-col w-full">
          <span className="text-gray-700 text-sm font-bold">Question Chapters</span>
          {props.selectedCourse.chapters === undefined ? <div>Not found!</div> :
          props.selectedCourse.chapters!.length === 0 ? <div>No Question types found!</div> :
          <select 
          onChange={(e) => {
            setFilterQuestionChapter(e.target.value);
          }}
          className="input-white border border-gray-400">
            <option>Choose Chapter</option>
            {props.selectedCourse.chapters!.map((item, i) => (
              <option key={i + 1} value={item.id}>{item.name}</option>
            ))}
          </select>}
        </div>
      </div>
      {loading === true ? <Loading /> :
      filteredQuestions(filterQuestionType, filterQuestionChapter, props.questionsList.filter(itm => itm.course === props.selectedCourse.course_id)).length === 0 ? <div>No questions uploaded!</div> :
      filteredQuestions(filterQuestionType, filterQuestionChapter, props.questionsList.filter(itm => itm.course === props.selectedCourse.course_id)).map((item, i) => (
        <QuestionItem 
          key={i + 1}
          question={item}
          callBack={props.callBack}
          status={props.questionPaper.find(itm => itm.id === item.id) ? true : false}
          questionTypes={props.questionTypes}
          select={true}
          number={i + 1} 
          questionsList={props.questionsList} />
      ))}
    </div>
  )
}


const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
}: StoreState): { auth: Auth; course: courseStoreInterface; schoolLevels: schoolLevelsStoreInterface; questionBank: QuestionBankStoreInterface; questionTypes: questionTypesStoreInterface; } => {
  return { auth: auth, course: course, schoolLevels: schoolLevels, questionBank: questionBank, questionTypes: questionTypes, };
};

const Questions = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  setSelectedCourse,
  loadQuestionTypes,
})(_Questions);

export default Questions;
